const util = {}

/**
 * @description æ›´æ–°æ ‡é¢˜
 * @param {String} title æ ‡é¢˜
 */
util.title = function (title) {
  window.document.title = title || 'bin-ui'
}

/**
 * @description æ‰“å¼€æ–°é¡µé¢
 * @param {String} url åœ°å€
 * @param target
 */
util.open = function (url, target = false) {
  let a = document.createElement('a')
  a.setAttribute('href', url)
  if (target) {
    a.setAttribute('target', '_blank')
  }
  a.setAttribute('id', 'b-link-temp')
  document.body.appendChild(a)
  a.click()
  document.body.removeChild(document.getElementById('b-link-temp'))
}

/**
 * æ—¶é—´æ ¼å¼åŒ–
 * @param time
 * @param cFormat
 * @returns {*}
 */
util.parseTime = function (time, cFormat) {
  if (arguments.length === 0) {
    return null
  }
  const format = cFormat || '{y}-{m}-{d} {h}:{i}:{s}'
  let date
  if (typeof time === 'object') {
    date = time
  } else {
    if (('' + time).length === 10) time = parseInt(time) * 1000
    date = new Date(time)
  }
  const formatObj = {
    y: date.getFullYear(),
    m: date.getMonth() + 1,
    d: date.getDate(),
    h: date.getHours(),
    i: date.getMinutes(),
    s: date.getSeconds(),
    a: date.getDay()
  }
  // eslint-disable-next-line
  const time_str = format.replace(/{(y|m|d|h|i|s|a)+}/g, (result, key) => {
    let value = formatObj[key]
    if (key === 'a') return ['ä¸€', 'äºŒ', 'ä¸‰', 'å››', 'äº”', 'å…­', 'æ—¥'][value - 1]
    if (result.length > 0 && value < 10) {
      value = '0' + value
    }
    return value || 0
  })
  // eslint-disable-next-line
  return time_str
}

/**
 * é˜²æŠ–å‡½æ•°ï¼Œè¿”å›å‡½æ•°è¿ç»­è°ƒç”¨æ—¶ï¼Œç©ºé—²æ—¶é—´å¿…é¡»å¤§äºæˆ–ç­‰äº waitï¼Œfunc æ‰ä¼šæ‰§è¡Œ
 *
 * @param  {function} func        å›è°ƒå‡½æ•°
 * @param  {number}   wait        è¡¨ç¤ºæ—¶é—´çª—å£çš„é—´éš”
 * @param  {boolean}  immediate   è®¾ç½®ä¸ºtureæ—¶ï¼Œæ˜¯å¦ç«‹å³è°ƒç”¨å‡½æ•°
 * @return {function}             è¿”å›å®¢æˆ·è°ƒç”¨å‡½æ•°
 */
util.debounce = function (func, wait = 50, immediate = true) {
  let timer, context, args
  // å»¶è¿Ÿæ‰§è¡Œå‡½æ•°
  const later = () => setTimeout(() => {
    // å»¶è¿Ÿå‡½æ•°æ‰§è¡Œå®Œæ¯•ï¼Œæ¸…ç©ºç¼“å­˜çš„å®šæ—¶å™¨åºå·
    timer = null
    // å»¶è¿Ÿæ‰§è¡Œçš„æƒ…å†µä¸‹ï¼Œå‡½æ•°ä¼šåœ¨å»¶è¿Ÿå‡½æ•°ä¸­æ‰§è¡Œ
    // ä½¿ç”¨åˆ°ä¹‹å‰ç¼“å­˜çš„å‚æ•°å’Œä¸Šä¸‹æ–‡
    if (!immediate) {
      func.apply(context, args)
      context = args = null
    }
  }, wait)

  // è¿™é‡Œè¿”å›çš„å‡½æ•°æ˜¯æ¯æ¬¡å®é™…è°ƒç”¨çš„å‡½æ•°
  return function (...params) {
    // å¦‚æœæ²¡æœ‰åˆ›å»ºå»¶è¿Ÿæ‰§è¡Œå‡½æ•°ï¼ˆlaterï¼‰ï¼Œå°±åˆ›å»ºä¸€ä¸ª
    if (!timer) {
      timer = later()
      // å¦‚æœæ˜¯ç«‹å³æ‰§è¡Œï¼Œè°ƒç”¨å‡½æ•°
      // å¦åˆ™ç¼“å­˜å‚æ•°å’Œè°ƒç”¨ä¸Šä¸‹æ–‡
      if (immediate) {
        func.apply(this, params)
      } else {
        context = this
        args = params
      }
      // å¦‚æœå·²æœ‰å»¶è¿Ÿæ‰§è¡Œå‡½æ•°ï¼ˆlaterï¼‰ï¼Œè°ƒç”¨çš„æ—¶å€™æ¸…é™¤åŸæ¥çš„å¹¶é‡æ–°è®¾å®šä¸€ä¸ª
      // è¿™æ ·åšå»¶è¿Ÿå‡½æ•°ä¼šé‡æ–°è®¡æ—¶
    } else {
      clearTimeout(timer)
      timer = later()
    }
  }
}

/**
 * æ´—ç‰Œå‡½æ•°
 * @param arr éœ€è¦æ´—ç‰Œçš„æ•°ç»„
 */
util.shuffle = function (arr) {
  let newArr = arr.slice()// å¤åˆ¶ä¸€ä¸ªæ–°æ•°ç»„
  for (let i = 0; i < newArr.length; i++) {
    let j = util.getRandomInt(0, i)// åœ¨0-å½“å‰å¾ªç¯çš„ä½ç½®éšæœºä¸€ä¸ªä½ç½®åšäº¤æ¢
    let t = newArr[i]
    newArr[i] = newArr[j]
    newArr[j] = t
  }
  return newArr
}

/**
 * åœ¨æŸä¸ªåŒºé—´éšæœºä¸€ä¸ªæ•´æ•°
 * @param min æœ€å°å€¼
 * @param max æœ€å¤§å€¼
 * @return {number}
 */
util.getRandomInt = function (min, max) {
  return Math.floor(Math.random() * (max - min + 1) + min)
}

/**
 * æ¸…ç©ºå›è½¦æ¢è¡Œç¬¦
 * @param str
 * @returns {*}
 */
util.replaceReturn = function (str) {
  return str ? str.replace(/(â†µ)+|(\n)+|(\r\n)+/g, '') : ''
}

/**
 * è¿”å›ä¸€ä¸ªæ°´å°canvas
 */
util.getWaterMark = function (str) {
  let waterMarkText = str || 'water-mark'

  let canvas = document.createElement('canvas')
  let ctx = canvas.getContext('2d')
  canvas.width = canvas.height = 100
  ctx.textAlign = 'center'
  ctx.textBaseline = 'middle'
  ctx.globalAlpha = 0.08
  ctx.font = '20px Microsoft Yahei'
  ctx.translate(50, 50)
  ctx.rotate(-Math.PI / 4)
  ctx.fillText(waterMarkText, 0, 0)
  return canvas
}

export default util
