import * as actionTypes from './actionTypes'
import { fromJS } from 'immutable'

// header ç‹¬ç«‹çš„çŠ¶æ€ç®¡ç†,ç±»ä¼¼äºvuexä¸­çš„moduleæ¨¡å—,å•ç‹¬ç®¡ç†è‡ªå·±çš„çŠ¶æ€å’Œaction
const defaultState = fromJS({
  focused: false,
  mouseIn: false,
  hotList: []
})

export default function (state = defaultState, action) {
  switch (action.type) {
    case actionTypes.SEARCH_FOCUS:
      return state.set('focused', true) // ç›¸å½“äºè¿”å›äº†ä¸€ä¸ªå…¨æ–°çš„æ•°æ®å¹¶è®¾ç½®,æ„Ÿè§‰è¿˜æ˜¯ç±»ä¼¼äºæ‹·è´
    case actionTypes.SEARCH_BLUR:
      return state.set('focused', false)
    case actionTypes.SET_HOT_LIST:
      // é—®é¢˜,åˆ›å»ºstateçš„æ—¶å€™ä¼šè½¬æ¢ä¸ºimmutableå¯¹è±¡,ä¸”,æ•°ç»„ä¹Ÿä¼šåˆå§‹åŒ–immutableæ•°ç»„
      return state.set('hotList', action.data)
    case actionTypes.MOUSE_ENTER:
      return state.set('mouseIn', true)
    case actionTypes.MOUSE_LEAVE:
      return state.set('mouseIn', false)
    default:
      return state
  }
}
