import { fromJS } from 'immutable'
import * as actionTypes from './actionTypes'

// header ç‹¬ç«‹çš„çŠ¶æ€ç®¡ç†,ç±»ä¼¼äºvuexä¸­çš„moduleæ¨¡å—,å•ç‹¬ç®¡ç†è‡ªå·±çš„çŠ¶æ€å’Œaction
const defaultState = fromJS({
  topicList: [],
  articleList: [],
  recommendList: [],
  writerList: [],
  showBackTop: false
})

export default function (state = defaultState, action) {
  switch (action.type) {
    case actionTypes.SET_HOME_DATA:
      //æ³¨æ„:è¿™é‡Œéœ€è¦å°†jsè·å–çš„æ™®é€šjså¯¹è±¡,è½¬æ¢ä¸ºimmutableå¯¹è±¡
      return state.merge({
        topicList: fromJS(action.data.topicList),
        articleList: fromJS(action.data.articleList),
        recommendList: fromJS(action.data.recommendList),
        writerList: fromJS(action.data.writerList)
      })
    case actionTypes.ADD_HOME_LIST:
      //æ³¨æ„:è¿™é‡Œéœ€è¦å°†jsè·å–çš„æ™®é€šjså¯¹è±¡,è½¬æ¢ä¸ºimmutableå¯¹è±¡
      return state.set('articleList', state.get('articleList').concat(action.list))
    case actionTypes.SET_BACK_TOP_SHOW:
      //æ³¨æ„:è¿™é‡Œéœ€è¦å°†jsè·å–çš„æ™®é€šjså¯¹è±¡,è½¬æ¢ä¸ºimmutableå¯¹è±¡
      return state.set('showBackTop', action.show)
    default:
      return state
  }
}
