// æ˜ å°„props immutable å¯¹è±¡ä½¿ç”¨getè·å–
const mapStateToProps = (state) => {
  return {
    focused: state.get('header').get('focused'),
    mouseIn: state.getIn(['header', 'mouseIn']),
    hotList: state.getIn(['header', 'hotList']),
    login: state.getIn(['login', 'login'])
  }
}
// æ˜ å°„dispatch
const mapDispatchToProps = (dispatch) => {
  return {
    handleInputFocus(list) {
      // æ³¨æ„,listä¸ºä¼ å…¥çš„immutableå¯¹è±¡hotList,è¿™é‡Œæœ‰sizeæ–¹æ³•è·å–é•¿åº¦
      if (list.size === 0) {
        // å‘é€å¼‚æ­¥è¯·æ±‚è·å–ä¸€æ¬¡åˆ—è¡¨
        dispatch(actionCreator.getHotList())
      }
      dispatch(actionCreator.getSearchFocusAction())
    },
    handleMouseEnter() {
      dispatch(actionCreator.mouseEnter())
    },
    handleMouseLeave() {
      dispatch(actionCreator.mouseLeave())
    },
    handleInputBlur() {
      dispatch(actionCreator.getSearchBlurAction())
    },
    handleChange() {
      dispatch(actionCreator.getHotList())
    },
    logout() {
      dispatch(loginActionCreator.logout())
    }
  }
}
// è¿æ¥
export default connect(mapStateToProps, mapDispatchToProps)(Header)
